.App{
  display: flex;
  flex-flow: column nowrap;
  height: 100vh;
  justify-content: space-between;
}

.App-header {
  display: flex;
  align-items: center;
  border-bottom: 5px solid rgb(226, 28, 28);
}

.App-header img {
  width: 20%;
}

.App-header h1{
  font-size: 3rem;
  font-weight: bold;
  color: rgb(226, 28, 28);
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App-body{
  height: 100%;
  padding: 40px;
  font-size: 24px;
}

.App-footer{
  text-align: center;
  border-top: 5px solid rgb(226, 28, 28);
  padding-top: 10px;
  font-family: cursive;
  font-size: 24px;
}